@media all and (min-width:0px){
    .secondLine {
        width: 100%;
        height: 160px;
        padding-top: 5px;
        font-size: medium;
        background: -webkit-gradient(linear, left top, right top, from(#e18a07), to(#ffcc00));
        background: -webkit-linear-gradient(left, #e18a07, #ffcc00);
        background: -o-linear-gradient(left, #e18a07, #ffcc00);
        background: linear-gradient(to right, #e18a07, #ffcc00);
        text-align: center;
        -ms-flex-pack: center;
            justify-content: center;
        -ms-flex-align: center;
            align-items: center;
        padding: 15px;
    }

    .secondLineTwo {
        width: 90%;
    }

    p {
        font-family: 'Open Sans';
        font-weight: lighter;
        color: #ffffff;
    }

    .checkmark {
        font-weight: bold;
        color: #49cc99;
    }
}

